import React, { Component, Suspense } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { Container } from "reactstrap";
import { connect } from "react-redux";
// sidebar nav config
import navigation from "../../_nav";
import { AppRoutes } from "../../Config";
// routes config
import routes from "../../routes";
import Loader from "../Loader/Loader";
import {
  AppBreadcrumb,
  AppFooter,
  AppHeader,
  AppSidebar,
  AppSidebarFooter,
  AppSidebarForm,
  AppSidebarHeader,
  AppSidebarMinimizer,
  AppSidebarNav,
} from "@coreui/react";
import {
  profileInfoRequest,
  modelOpenRequest,
  logOutRequest,
  redirectTo,
} from "../../actions";

const DefaultFooter = React.lazy(() => import("./DefaultFooter"));
const DefaultHeader = React.lazy(() => import("./DefaultHeader"));

class DefaultLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: true,
      isAuthenticated: true,
      userDetails: {},
    };
  }
  componentDidMount() {
    if (!localStorage.getItem("adminToken")) {
      this.props.onGoPage("/login");
    } else {
      this.props.profileInfoAction();
    }
  }
  componentDidUpdate({ location }) {
    const { pathname } = location;
    if (pathname !== this.props.location.pathname) {
      this.setState({
        isAuthenticated: true,
      });
      this.props.profileInfoAction();
    }
  }
  signOut(e) {
    e.preventDefault();
    this.props.onLogout();
  }
  render() {
    return (
      <>
        <div className='app'>
          <AppHeader fixed>
            <Suspense fallback={<Loader />}>
              <DefaultHeader
                history={this.props.history}
                onLogout={(e) => this.signOut(e)}
                userDetails={this.state.userDetails}
              />
            </Suspense>
          </AppHeader>
          <div className='app-body'>
            <AppSidebar className='custom-sidebar' fixed display='lg'>
              <AppSidebarHeader />
              <AppSidebarForm />
              <Suspense>
                <AppSidebarNav navConfig={navigation} {...this.props} isOpen />
              </Suspense>
              <AppSidebarFooter />
              <AppSidebarMinimizer />
            </AppSidebar>
            <main className='main'>
              <AppBreadcrumb appRoutes={routes} />
              <Container fluid>
                <Suspense fallback={<Loader />}>
                  <Switch>
                    {routes.map((route, idx) => {
                      return route.component ? (
                        <Route
                          key={idx}
                          path={route.path}
                          exact={route.exact}
                          name={route.name}
                          render={(props) => <route.component {...props} />}
                        />
                      ) : null;
                    })}
                    <Redirect
                      from={AppRoutes.HOME.url}
                      to={AppRoutes.DASHBOARD.url}
                    />
                  </Switch>
                </Suspense>
              </Container>
            </main>
          </div>
          <AppFooter>
            <Suspense fallback={<Loader />}>
              <DefaultFooter />
            </Suspense>
          </AppFooter>
        </div>
      </>
    );
  }
}

const mapStateToProps = (state) => ({
  profileInfoReducer: state.profileInfoReducer,
  modelInfoReducer: state.modelInfoReducer,
});

const mapDispatchToProps = (dispatch) => ({
  profileInfoAction: () => {
    dispatch(profileInfoRequest());
  },
  modelOperate: (data) => {
    dispatch(modelOpenRequest({ modelDetails: data }));
  },
  onLogout: (data) => {
    dispatch(logOutRequest(data));
  },
  onGoPage: (data) => {
    dispatch(redirectTo({ path: data }));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(DefaultLayout);
