import React from "react";
import { AppRoutes } from "./Config";

const Dashboard = React.lazy(() => import("./views/Dashboard/Dashboard"));
const MyProfile = React.lazy(() => import("./containers/Profile"));
const Users = React.lazy(() => import("./containers/Users"));
const Sets = React.lazy(() => import("./containers/Sets"));
const Folders = React.lazy(()=> import("./containers/Folders"));

const routes = [
  {
    path: AppRoutes.HOME.url,
    exact: AppRoutes.HOME.exact,
    name: AppRoutes.HOME.name,
  },
  {
    path: AppRoutes.DASHBOARD.url,
    name: AppRoutes.DASHBOARD.name,
    exact: AppRoutes.DASHBOARD.exact,
    component: Dashboard,
  },
  {
    path: AppRoutes.PROFILE.url,
    exact: AppRoutes.PROFILE.exact,
    name: AppRoutes.PROFILE.name,
    component: MyProfile,
  },
  {
    path: AppRoutes.USERS.url,
    exact: AppRoutes.USERS.exact,
    name: AppRoutes.USERS.name,
    component: Users,
  },
  {
    path: AppRoutes.SETS.url,
    exact: AppRoutes.SETS.exact,
    name: AppRoutes.SETS.name,
    component: Sets,
  },
  {
    path: AppRoutes.FOLDERS.url,
    exact: AppRoutes.FOLDERS.exact,
    name: AppRoutes.FOLDERS.name,
    component: Folders,
  }
];

export default routes;
